import stylez from "./Controller.module.css"
import Button from "./Button"
import { useState, } from 'react'

const keyz = [{
    keyCode: 81,
    keyTrigger: 'Q',
    id: 'Heater-1',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3'
  },
  {
    keyCode: 87,
    keyTrigger: 'W',
    id: 'Heater-2',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3'
  },
  {
    keyCode: 69,
    keyTrigger: 'E',
    id: 'Heater-3',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3'
  },
  {
    keyCode: 65,
    keyTrigger: 'A',
    id: 'Heater-4',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3'
  },
  {
    keyCode: 83,
    keyTrigger: 'S',
    id: 'Clap',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3'
  },
  {
    keyCode: 68,
    keyTrigger: 'D',
    id: 'Open-HH',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3'
  },
  {
    keyCode: 90,
    keyTrigger: 'Z',
    id: "Kick-n'-Hat",
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3'
  },
  {
    keyCode: 88,
    keyTrigger: 'X',
    id: 'Kick',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3'
  },
  {
    keyCode: 67,
    keyTrigger: 'C',
    id: 'Closed-HH',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3',
  }
];

function Controller (){
    const [volume, setVolume] = useState(0.5);
    const [nametag, setNametag] = useState("");
    

    return (<div className = {stylez.box} id="display">
        <div className = {stylez.leftside}>
            {keyz.map(clip => (
                <Button key = {clip.id} clip = {clip} volume = {volume} id={clip.id} setname = {setNametag}/>
            ))}
        </div>
        <div className = {stylez.rightside}>
        <div className = {stylez.flex}>Volume
        <input type="range" step="0.01" max = "1" min = "0" value = {volume} onChange = {(e) => setVolume(e.target.value)}/>
        </div>
            <span>{nametag}</span>
        </div>

    </div>)
}

export default Controller