import { useEffect, useState } from 'react';

function Button(props){

    const [active, setActive] = useState(false);

    useEffect(() => {
        
        document.addEventListener("keydown", handleKeyPress);
        return () => {
            document.removeEventListener("keydown", handleKeyPress)
        }

      },);

    const handleKeyPress = (keyboard) => {
        if (keyboard.keyCode === props.clip.keyCode){
            playSound(props.volume)
        }
    }

    function playSound(){
        const audioTag = document.getElementById(props.clip.keyTrigger);
        const buttonFocus = document.getElementById(props.id);
        buttonFocus.focus();
        setTimeout(() => buttonFocus.blur(), 50);
        setActive(true);
        setTimeout(() => setActive(false), 200)
        audioTag.volume = props.volume;
        audioTag.currentTime = 0;
        props.setname(props.clip.id);
        audioTag.play();
    
    }
    return ( <button className = "drum-pad" onClick = {playSound} id = {props.id}>
        <audio className = "clip" id = {props.clip.keyTrigger} src = {props.clip.url}/>{props.clip.keyTrigger}
    </button>)
}

export default Button